@import url(https://fonts.googleapis.com/css2?family=Libre+Franklin:wght@400&display=swap&family=Montserrat&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.logoTitle {
  font-family: "Libre Franklin", "Arial", sans-serif;
  font-size: 2em;
}

.globalContainer {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
.pageContainer {
  width: 100%;
  position: relative;
  top: 2rem;
}
/* ------ HEADER ------ */

header {
  background-color: rgb(247, 247, 247);
  box-sizing: border-box;
  padding: 1.5rem;
  height: auto;
  width: 100%;
  max-height: 12rem;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  font-family: "Libre Franklin", "Arial", sans-serif;
  font-size: 0.8rem;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.19), 0 6px 6px rgba(0, 0, 0, 0.23);
}

header .logoTitle {
  max-width: 450px;
  color: transparent;
  font-size: 0.1rem;
}
header .logoTitle,
header .logoTitle a,
header .logoTitle a img {
  box-sizing: border-box;
  width: 100%;
  margin: 0;
}

header nav ul li {
  margin: 1rem 0.5rem;
  padding: 1rem 3rem;
  background: linear-gradient(-45deg, #eee, #fff, #eee);
  border: solid 0.5px #fff;
  border-radius: 2em 2%;
  box-shadow: rgba(50, 50, 93, 0.25) 0px 2px 5px -1px,
    rgba(0, 0, 0, 0.3) 0px 1px 3px -1px;
  color: #000;
  cursor: pointer;
  text-align: center;
}

header nav ul li a {
  text-decoration: none;
  color: #000;
}
header .logo {
  margin-bottom: 1rem;
  max-width: 400px;
}
label {
  color: transparent;
  font-size: 0.1rem;
}
header .search {
  width: 80%;
  font-size: 1rem;
  height: 1.5rem;
  padding: 0.4rem;
  margin: 1rem 5%;
}

header .search::-webkit-input-placeholder {
  color: rgba(107, 107, 107, 0.678);
}

header .search:-ms-input-placeholder {
  color: rgba(107, 107, 107, 0.678);
}

header .search::placeholder {
  color: rgba(107, 107, 107, 0.678);
}

