header .navbar {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.menu {
  width: 100%;
  display: grid;
  grid-template-columns: 100%;
  grid-gap: 0;
  z-index: 10;
}
.menu-toggle {
  border: none;
  width: 4rem;
  margin: 0 auto;
  font-size: 3rem;
  color: #000;
}
.menu-toggle:hover,
.menu-list:hover {
  cursor: pointer;
}
.menu-list {
  list-style: none;
  display: grid;
  grid-template-columns: 100%;
  grid-gap: 0;
  z-index: 10;
}
.menu-list li {
  padding: 0.5rem 0;
  text-align: center;
}
.hidden {
  display: none;
}

