/* ------------ */

footer {
  background-color: rgb(247, 247, 247);
  width: 100%;
  position: fixed;
  bottom: 0;
  font-family: "Montserrat", "Arial", sans-serif;
  font-size: 0.8rem;
}

footer ul {
  width: 100%;
  display: flex;
  justify-content: space-evenly;
}
footer ul li {
  padding: 1rem 0;
}
footer a {
  text-decoration: none;
  color: #333;
}

@media only screen and (min-width: 1200px) {
  header nav label {
    display: none;
  }
  header nav,
  nav ul {
    display: flex;
  }
  header nav ul {
    position: relative;
  }
  header nav ul {
    width: 100%;
    top: auto;
    max-height: none;
    background: none;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
  }
  header nav ul li {
    margin: 1rem 0.5rem;
    padding: 1rem 3rem;
    background: linear-gradient(-45deg, #eee, #fff, #eee);
    border: solid 0.5px #fff;
    border-radius: 2em 2%;
    box-shadow: rgba(50, 50, 93, 0.25) 0px 2px 5px -1px,
      rgba(0, 0, 0, 0.3) 0px 1px 3px -1px;
    color: #000;
    cursor: pointer;
    text-align: center;
  }
  header nav ul li:hover {
    border: solid 1px rgb(141, 141, 141);
  }
  header .search {
    width: 90%;
    margin: 1rem 5%;
  }
  header .navbar {
    width: 60%;
    display: flex;
    flex-direction: column;
  }
  .menu-toggle {
    display: none;
  }
  .menu ul {
    list-style: none;
    display: grid;
    grid-template-columns: 25% 25% 25% 25%;
    grid-gap: 0;
    z-index: 10;
  }
  .menu ul {
    padding: 0.5rem 0;
    text-align: center;
  }
}

.Home {
  text-align: center;
}
.Home a {
  text-decoration: none;
  color: #000;
}
.Home > section {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  text-align: center;
  margin-top: 5%;
}
.animation-phare {
  width: 90%;
  margin: 0 5% 2% 5%;
  height: auto;
  position: relative;
}
.animation-phare img {
  width: 100%;
  height: 100%;
  max-height: 20rem;
  object-fit: cover;
  object-position: 0% 20%;
}
.animation-phare p,
.animation-dual p {
  position: absolute;
  bottom: 1rem;
  left: 0;
  width: 40%;
  padding: 1rem 1rem;
  text-align: left;
  background-color: rgba(255, 255, 255, 0.648);
}
.animation-dual-group {
  width: 90%;
  max-height: 20rem;
  margin: 0 5% 0 5%;
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  justify-content: space-between;
}
.animation-dual {
  width: 48%;
  height: auto;
  position: relative;
}
.animation-dual a {
  width: 100%;
}
.animation-dual img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: 0% 0%;
}
.Home .productImg {
  width: 300px;
  height: auto;
  border: solid 10px #000;
}
.Home h2 {
  width: 100%;
  margin: 5% 0 2% 0;
  font-size: 1rem;
  text-align: left;
  padding-bottom: 0.3rem;
  border-bottom: solid 3px #000;
}
.Home h3 {
  font-size: 1rem;
  padding: 0.4rem;
}
.Home .bests {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  margin: 5% 5% 2% 5%;
}
.Home .best-product-list {
  width: 100%;
  margin: 2% 0 2% 0;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: flex-start;
}
.Home .best-product-list > a {
  display: inline-block;
  width: 100%;
  padding: 0.5rem;
  margin: 5% 1%;
  box-shadow: rgba(50, 50, 93, 0.25) 0px 2px 5px -1px,
    rgba(0, 0, 0, 0.3) 0px 1px 3px -1px;
  font-size: 1.4rem;
}
.Home .best-product-card {
  width: 50%;
}
.Home .best-product-card img {
  width: 80%;
}
.Home .best-product-card .best-product-link {
  text-decoration: none;
  display: inline-block;
  width: 80%;
  box-shadow: rgba(50, 50, 93, 0.25) 0px 2px 5px -1px,
    rgba(0, 0, 0, 0.3) 0px 1px 3px -1px;
  background-color: #000;
  color: #fff;
  font-size: 1.1rem;
  padding: 0.3rem;
}

.Home .chapo {
  width: 90%;
  text-align: justify;
  margin: 10% 5% 15% 5%;
  color: rgb(109, 109, 109);
}

@media only screen and (min-width: 800px) {
  .Home h2 {
    width: 96%;
    margin: 0 2% 2% 2%;
    font-size: 1.2rem;
  }
  .Home > section {
    width: 90%;
    margin: 1% 5% 2% 5%;
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    justify-content: space-between;
  }
  .animation-phare {
    width: 69%;
    margin: 0;
  }
  .animation-dual-group {
    width: 30%;
    margin: 0;
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
  }
  .animation-dual {
    width: 100%;
    max-height: 48%;
    margin: 0;
  }
  .Home .bests {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    margin: 0 5% 1% 5%;
  }
  .Home .best-product-list {
    width: 50%;
    margin: 0 0% 2% 0%;
  }
  .Home .chapo {
    width: 90%;
    text-align: justify;
    margin: 0 5% 7% 5%;
    color: rgb(136, 136, 136);
  }
}

.categoryContainer {
  box-sizing: border-box;
  display: flex;
  flex-flow: column nowrap;
  width: 90%;
  margin: 0 5% 15rem 5%;
}
.categoryContainer nav {
  width: 100%;
  text-align: left;
  margin-bottom: 1rem;
}
.categoryContainer h1 {
  width: 100%;
  margin-bottom: 1rem;
  font-size: 1rem;
  text-align: left;
  padding-bottom: 0.3rem;
  border-bottom: solid 3px #000;
}
.categoryContainer p {
  text-align: justify;
  padding: 0 0.5rem;
  margin-bottom: 1rem;
}
.multipleCategoriesContainer,
.multipleProductsContainer {
  box-sizing: border-box;
  display: flex;
  flex-flow: row wrap;
  justify-content: space-around;
  align-items: flex-start;
}

.multipleCategoriesContainer article {
  box-sizing: border-box;
  width: 45%;
  height: 20rem;
  margin: 0.25rem 0;
  padding-top: 0.25rem;
  display: flex;
  flex-flow: column nowrap;
  justify-content: space-between;
  align-items: center;
}
.multipleCategoriesContainer article a,
.multipleProductsContainer article a {
  text-decoration: none;
  color: #000;
}
.multipleCategoriesContainer article p,
.multipleProductsContainer article p {
  text-align: center;
  padding: 0 0.25rem;
}
.multipleCategoriesContainer article img,
.multipleProductsContainer article img {
  max-width: 100%;
  max-height: 70%;
  margin: 0.5rem 0;
  border: 0.5rem solid #000;
}

.categoryContainer .categoryInformation {
  width: 100%;
  text-align: justify;
  margin: 10% 0 10% 0;
  color: rgb(109, 109, 109);
  padding: 0;
}
.best-product-link {
  text-decoration: none;
  display: inline-block;
  width: 100%;
  box-shadow: rgba(50, 50, 93, 0.25) 0px 2px 5px -1px,
    rgba(0, 0, 0, 0.3) 0px 1px 3px -1px;
  background-color: #000;
  color: #fff;
  font-size: 1.1rem;
  text-align: center;
  padding: 0.3rem;
  margin: 0 2%;
}
.multipleProductsContainer .productCard {
  width: 43%;
  margin-bottom: 2rem;
}
.multipleProductsContainer .productCard div {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: flex-end;
}
.multipleProductsContainer .productCard div h3 {
  font-size: 1rem;
}
.multipleProductsContainer .productCard div p {
  font-weight: bold;
  margin: 0;
}

@media only screen and (min-width: 800px) {
  .categoryContainer h1 {
    font-size: 1.2rem;
  }
  .multipleCategoriesContainer article,
  .multipleProductsContainer article {
    margin: 2rem 0;
    width: 22%;
  }
  .categoryContainer .categoryInformation {
    margin: 2% 0 5% 0;
  }
  .multipleProductsContainer .productCard {
    width: 14%;
    margin: 2rem 2%;
  }
  .multipleCategoriesContainer article a div h3 {
    font-size: 1.2rem;
  }
}

.Product {
  width: 90%;
  margin: 1% 5% 20% 5%;
}
.Product article {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  align-items: flex-start;
  margin: 1rem;
}
.Product article div {
  width: 100%;
  margin: 2rem 0;
}
.Product article h2 {
  font-size: 2rem;
}
.Product article img {
  width: 100%;
  border: solid 6px #000;
}
.Product .productDescription {
  margin: 1rem 0;
}
.Product .priceBasket {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
}
.Product .productPrice {
  font-size: 3rem;
  font-weight: bold;
}
.Product .productPrice span {
  font-size: 0.6em;
  bottom: 1rem;
}
.Product .btnBasket {
  background-color: #000;
  color: #fff;
  text-decoration: none;
  padding: 1rem;
  font-size: 1.5rem;
  text-transform: uppercase;
}
.Product .light {
  font-weight: normal;
}

@media only screen and (min-width: 800px) {
  .Product article {
    flex-direction: row-reverse;
    flex-wrap: wrap;
    justify-content: flex-end;
    margin: 4rem 1rem;
  }
  .Product article div {
    width: 65%;
    margin: 0;
  }
  .Product article img {
    width: 28%;
    margin: 0 2%;
  }
}

.guideContainer {
  box-sizing: border-box;
  display: flex;
  flex-flow: column nowrap;
  padding: 0 0.5rem 3rem 0.5rem;
}
.guideContainer nav {
  width: 100%;
  text-align: left;
  margin-bottom: 1rem;
}
.guideContainer nav a {
  color: #000;
}
.guideContainer h2 {
  width: 100%;
  margin-bottom: 1rem;
  font-size: 1rem;
  text-align: left;
  padding-bottom: 0.3rem;
  border-bottom: solid 3px #000;
}
.guideContainer p {
  text-align: justify;
  padding: 0 0.5rem;
  margin-bottom: 1rem;
}
.multipleGuidesContainer {
  box-sizing: border-box;
  display: flex;
  flex-flow: row wrap;
  justify-content: space-around;
  align-items: flex-start;
}

.multipleGuidesContainer article {
  box-sizing: border-box;
  width: 45%;
  height: 10rem;
  margin: 0.25rem 0;
  padding-top: 0.25rem;
  display: flex;
  flex-flow: column nowrap;
  justify-content: space-between;
  align-items: center;
}
.multipleGuidesContainer article a {
  text-decoration: none;
  color: #000;
}
.multipleGuidesContainer article p {
  text-align: center;
  padding: 0.5rem 0.25rem;
}
.multipleGuidesContainer article img {
  max-width: 90%;
  max-height: 70%;
  margin: 0.5rem 0;
  border: 0.5rem solid #000;
}
@media only screen and (min-width: 800px) {
  .multipleGuidesContainer article {
    margin: 2rem 0;
    width: 22%;
  }
}

